import React, {Component} from 'react';
import './App.css';

const GameStateEnum = {
    started: "started",
    running: "running",
    lost: "lost",
    won: "won",
};
Object.freeze(GameStateEnum);

function Trace(props) {
    const number1 = props.move.chosenNumbers.length >=1 ? props.move.chosenNumbers[0].n : "?";
    const number2 = props.move.chosenNumbers.length >=2 ? props.move.chosenNumbers[1].n : "?";
    return (
        <div> Trace:
            <div>{number1} {props.move.operator} {number2} {props.move.gameState}</div>
        </div>
    )
}

function UndoMove(props) {
    const btnEnabled = props.gameState === GameStateEnum.running;
    return btnEnabled ? (
            <i onClick={props.onClick} className="material-icons"
               style={{
                   fontSize: 48,
                   color: 'green',
                   cursor: 'pointer',
               }}>undo</i>
        ) :
        (
            <i onClick={props.onClick} className="material-icons"
               style={{
                   fontSize: 48,
                   color: 'gray',
               }}>undo</i>
        )
}

function Undo(props) {
    const btnEnabled = props.gameState === GameStateEnum.running;
    return btnEnabled ?
        (
            <i onClick={props.onClick} className="material-icons"
               style={{
                   fontSize: 48,
                   color: 'orange',
                   cursor: 'pointer',
                   display: props.gameState === GameStateEnum.running ? "" : "none"
               }}>skip_previous</i>
        )
        :
        (
            <i onClick={props.onClick} className="material-icons"
               style={{
                   fontSize: 48,
                   color: 'gray',
               }}>skip_previous</i>
        )

}

function Skip(props) {
    const btnEnabled = props.gameState === GameStateEnum.running || props.gameState === GameStateEnum.started;
    return btnEnabled ?
        (
            <i onClick={props.onClick}
               className="material-icons"
               style={{
                   fontSize: 48,
                   color: 'red',
                   cursor: 'pointer',
                   display: props.gameState === GameStateEnum.running || props.gameState === GameStateEnum.started ? "" : "none"
               }}>skip_next</i>
        )
        :
        (
            <i onClick={props.onClick}
               className="material-icons"
               style={{
                   fontSize: 48,
                   color: 'gray',
               }}>skip_next</i>
        )

}

function Next(props) {
    const btnEnabled = props.gameState === GameStateEnum.won || props.gameState === GameStateEnum.lost;
    return btnEnabled ?
        (
            <i onClick={props.onClick}
               className="material-icons"
               style={{
                   fontSize: 48,
                   color: 'green',
                   cursor: 'pointer',
               }}>navigate_next</i>
        )
        :
        (
            <i
                className="material-icons"
                style={{
                    fontSize: 48,
                    color: 'gray',
                }}>navigate_next</i>
        )
}

function GameButtons(props) {
    return (
        <div>
            <Undo onClick={props.onUndo} gameState={props.gameState}/>
            &nbsp;
            <UndoMove onClick={props.onUndoMove} gameState={props.gameState}/>
            &nbsp;
            <Skip onClick={props.onSkip} gameState={props.gameState}/>
            &nbsp;
            <Next onClick={props.onNext} gameState={props.gameState}/>
        </div>
    )
}

function GameState(props) {
    if (props.gameState == GameStateEnum.won) {
        return (
            <i className="material-icons"
               style={{
                   fontSize: 48,
                   color: 'green',
               }}>mood</i>
        )
    } else if (props.gameState == GameStateEnum.lost) {
        return (
            <i className="material-icons"
               style={{
                   fontSize: 48,
                   color: 'yellow',
               }}>sentiment_very_dissatisfied</i>
        )
    } else {
        return <div></div>
    }
}

function Operator(props) {
    function onClickOperator() {
        props.onClickOperator(props.operator)
    }

    return (
        <button onClick={onClickOperator} className="operatorButton" type="button">{props.operator} </button>
    )
}

function Number(props) {
    function onClickNumber() {
        props.onClickNumber(props.number)
    }

    const btnColor = props.gameState == GameStateEnum.won ? "green" : props.gameState == GameStateEnum.lost ? "red" : "black";
    return (
        <button onClick={onClickNumber} className="numberButton" type="button"
                style={{color: btnColor}}>{props.number.n}  </button>
    )
}

function Operators(props) {
    return (
        props.operators.map(o =>
            <Operator key={o} operator={o} onClickOperator={props.onClickOperator}/>
        )
    )
}

function Numbers(props) {
    let numberKey = 0;
    return (
        props.numbers.map(n =>
            <Number key={numberKey++} number={n} onClickNumber={props.onClickNumber} gameState={props.gameState}/>
        )
    )
}

class App extends Component {

    random = (min, max) => min + Math.floor(Math.random() * (max - min + 1));

    init = () => {
        this.chosenNumbers = [];
        this.availableNumbers = [];
        this.availableNumbers.push({n: this.random(1, 10), chosen: false})
        this.availableNumbers.push({n: this.random(1, 10), chosen: false})
        this.availableNumbers.push({n: this.random(1, 10), chosen: false})
        this.availableNumbers.push({n: this.random(1, 10), chosen: false})

        this.originalAvailableNumbers = [...this.availableNumbers];

        this.availableOpeartors = ["+", "-", "x", ":"];

        this.state = {
            chosenNumbers: [],
            operator: null,
            gameState: GameStateEnum.started,
            availableNumbers: this.availableNumbers
        };
    }

    constructor(props) {
        super(props);

        this.init()
    }

    plusFunction = (a, b) => a + b;
    minusFunction = (a, b) => a - b;
    multiplyFunction = (a, b) => a * b;
    divisionFunction = (a, b) => a / b;

    mapOperatorToFunction = {
        "+": this.plusFunction,
        "-": this.minusFunction,
        "x": this.multiplyFunction,
        ":": this.divisionFunction
    }

    executeMove = () => {
        const move = this.state;
        console.log("executeMove:");
        console.table(move);
        if (!(move.operator && move.chosenNumbers.length == 2)) {
            console.log("missing inputs");
            return;
        }

        console.log("all inputs set");

        const number1 = move.chosenNumbers[0];
        const number2 = move.chosenNumbers[1];

        const removeIndex1 = this.availableNumbers.indexOf(number1);
        this.availableNumbers.splice(removeIndex1, 1);

        const removeIndex2 = this.availableNumbers.indexOf(number2);
        this.availableNumbers.splice(removeIndex2, 1);

        const result = this.mapOperatorToFunction[move.operator](number1.n, number2.n)
        console.log("result=" + result);
        this.availableNumbers.push({n: result, chosen: false});
        this.chosenNumbers = [];

        if (this.availableNumbers.length === 1) {
            if (result === 24) {
                console.log("You won");
                this.setState({
                    chosenNumbers: this.chosenNumbers,
                    gameState: GameStateEnum.won,
                    availableNumbers: this.availableNumbers
                });

            } else {
                console.log("You lost");
                this.setState({
                    chosenNumbers: this.chosenNumbers,
                    gameState: GameStateEnum.lost,
                    availableNumbers: this.availableNumbers
                });
            }

            return;
        }

        this.setState({
            chosenNumbers: this.chosenNumbers,
            operator: null,
            gameState: GameStateEnum.running,
            availableNumbers: this.availableNumbers
        });
    }

    onClickNumber = n => {
        const move = this.state;
        // console.log("Number clicked move: " + move);
        console.log("Number clicked: " + n);
        if (n.chosen) {
            console.log("already chosen: " + n);
            return;
        }
        if (move.chosenNumbers.length >= 2) {
            console.log("max nrs chosen");
            return;
        }
        n.chosen = true;
        move.chosenNumbers.push(n);

        this.setState({
            chosenNumbers: move.chosenNumbers,
            operator: move.operator,
            gameState: GameStateEnum.running,
            availableNumbers: this.availableNumbers
        }, this.executeMove);
        console.log("number chosen: " + n.n);
    }

    onUndoMove = () => {
        this.availableNumbers.forEach(n => n.chosen = false);
        this.chosenNumbers = [];
        this.setState({
            chosenNumbers: this.chosenNumbers,
            operator: null,
            gameState: this.availableNumbers.length === this.originalAvailableNumbers ? GameStateEnum.started : GameStateEnum.running,
            availableNumbers: this.availableNumbers
        });
    }

    onUndo = () => {
        this.availableNumbers = [...this.originalAvailableNumbers];
        this.onUndoMove()
    }

    onClickOperator = operator => {
        const move = this.state;
        console.log("operator clicked: " + operator);
        this.setState({
            chosenNumbers: move.chosenNumbers,
            operator: operator,
            gameState: GameStateEnum.running,
            availableNumbers: this.availableNumbers
        }, this.executeMove);
        console.log("operator=" + operator);

    }

    onSkip = () => {
        this.init()
        this.setState({
            chosenNumbers: [],
            operator: null,
            gameState: GameStateEnum.started,
            availableNumbers: this.availableNumbers
        });
    }

    onNext = () => {
        this.init()
        this.setState({
            chosenNumbers: [],
            gameState: GameStateEnum.started,
            availableNumbers: this.availableNumbers
        });
    }

    render() {
        return (
            <div className="App">
                <p/>
                <Numbers numbers={this.state.availableNumbers} onClickNumber={this.onClickNumber}
                         gameState={this.state.gameState}/>

                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;

                <Operators operators={this.availableOpeartors} onClickOperator={this.onClickOperator}/>

                <GameButtons onUndo={this.onUndo} onUndoMove={this.onUndoMove} onSkip={this.onSkip} onNext={this.onNext}
                             gameState={this.state.gameState}/>
                <Trace move={this.state}/>
                <GameState gameState={this.state.gameState}/>
            </div>
        )
    }
}

export default App;

